.graBtn {
  position: relative;
  height: 60px;
  width: 200px;
  border: none;
  outline: none;
  color: white;
  background: #099e96;
  cursor: pointer;
  border-radius: 5px;
  font-size: 18px;
  font-family: "Raleway", sans-serif;
}
.graBtn:before {
  position: absolute;
  content: "";
  top: -2px;
  left: -2px;
  height: calc(100% + 4px);
  width: calc(100% + 4px);
  border-radius: 5px;
  z-index: -1;
  opacity: 0;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  background: linear-gradient(
    45deg,
    #4e4d4d,
    #8a8786,
    #4b4b44,
    #a4a7a3,
    #616362,
    #76777c,
    #817f85,
    #41393f,
    #807373
  );
  background-size: 400%;
  transition: opacity 0.3s ease-in-out;
  -webkit-animation: animate 20s linear infinite;
          animation: animate 20s linear infinite;
}
.graBtn:hover:before {
  opacity: 1;
}
.graBtn:hover:active {
  background: none;
}
.graBtn:hover:active:before {
  -webkit-filter: blur(2px);
          filter: blur(2px);
}
@-webkit-keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

